<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-2 mt-2">
                <LeftColumn />
            </div>
            <div class="col-10">
                <!-- <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
                    IdentityColumn="currency_log_id" :HeaderText="$t('CurrencyManagement')" :ListData="CurrencyManagementData" :HeaderData="filteredHeaders"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
                    SearchPosition="top" :SearchFields="topSearchFields"
                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'status_id'"> 
                            <listing-status :isfor="'COMPANYSETUP_UPDATE'" v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')"
                                ref="updatestatus" v-bind:dataid="data.row.currency_log_id" data-controller="COMPANYSETUP_UPDATE"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.row.currency_log_id" data-action="index" module="CurrencyManagement" subModule="CurrencyManagement"
                                v-bind:dataselected="data.row.status_id.value == 1001 ? 'Active' : 'Inactive'"
                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                            </listing-status>
                        </template>
                    </template>
                </tg-list> -->
                <div class="padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('CurrencyManagement') }}</span>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-4">
                        <div class="form-group">
                            <label>Company's Base Currency:<span class="text-danger">*</span></label>
                            <select class="form-control mr-3" name="select" v-model="selectedCurrency" @change="ChangeCurrencyUpdate(selectedCurrency)">
                                <option value="" disabled :selected="selectedCurrency">Select</option>
                                <option v-for="option in CurrencyList" :value="option.value">{{ option.name }}</option>
                                <!-- <option v-for="option in StatusList"
                                    v-bind:selected="RoleDetails.StatusId == option.STATUS_ID"
                                    v-bind:value="option.STATUS_ID">{{ option.STATUS }}</option> -->
                            </select>
                        </div>
                    </div>
                    <div class="listing mt-3 px-3">
                        <div class="table-responsive">
                            <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                                id="tbContainer">
                                <thead class="thead-bg">
                                    <tr>
                                        <th v-for="(item, index) in Headers" :key="index">{{ item }}:</th>
                                        <th>Automatic:</th>
                                        <th class="text-center single-action">
                                            <div @click="AddRow()" class="round-icon-small btn-success-light d-block float-none m-auto" style="line-height: 33px;" title="Add">
                                                <i class="fa fa-plus"></i>
                                            </div>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>      
                                    <tr class="mainleveldiv" v-for="(item, index) in CurrencyManagementData" :key="index">
                                        <!-- <template v-if="index == 0">        -->
                                            <td class="d-flex">
                                                <select class="form-control mr-3" name="select" :name="'select_' + index">
                                                    <option>Select</option>
                                                    <option v-for="option in CurrencyList" :value="option.value">{{ option.name }}</option>
                                                    <!-- <option v-for="option in StatusList"
                                                        v-bind:selected="RoleDetails.StatusId == option.STATUS_ID"
                                                        v-bind:value="option.STATUS_ID">{{ option.STATUS }}</option> -->
                                                </select>
                                                <input type="text" class="form-control" placeholder="Cost" v-model="JSON.parse(item.source_currency_id).value" disabled/>
                                            </td>
                                            <td>
                                                <select class="form-control mr-3" name="select" v-model="selectedCurrency" disabled>
                                                    <option v-for="option in CurrencyList" :value="option.value">{{ option.name }}</option>
                                                    <!-- <option v-for="option in StatusList"
                                                        v-bind:selected="RoleDetails.StatusId == option.STATUS_ID"
                                                        v-bind:value="option.STATUS_ID">{{ option.STATUS }}</option> -->
                                                </select>
                                            </td>
                                            <td>
                                                <input type="text" class="form-control" placeholder="Rate"  v-model="item.currency_rate" />
                                            </td>
                                            <td class="text-center">
                                                <div class="form-check form-check-inline  checkbox-inline font-weight-light">
                                                    <div class="custom-control custom-checkbox">
                                                        <input class="form-check-input custom-control-input" type="checkbox" :id="'chk_' + index"/>
                                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_' + index"></label>
                                                    </div>
                                                </div>
                                            </td>
                                            <td class='text-center single-action'>
                                                <div id='btnRemove' @click="DeleteRow(index)"
                                                    class='round-icon-small btn-danger-light d-block float-none m-auto'
                                                    title='Remove'>
                                                    <i class='fa fa-trash'></i>
                                                </div>
                                            </td>
                                        <!-- </template> -->
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!---modal popup-->
        <AddCompanySetup :recordId="recordId" :title="$t('CAManageCurrencyManagement')" v-if="showCurrencyManagement" v-on:close="showCurrencyManagement=false;recordId=0" v-on:addReloadData="FetchData();showCurrencyManagement=false;recordId=0" v-bind:moduleName="'CurrencyManagement'" v-bind:emitName="'addReloadData'" v-bind:addApiUrl="'/CurrencyManagement/add'" v-bind:subModule="'CurrencyManagement'" />
    </div>
</template>
<script>
import CurrencyManagementDataService from "@/services/DataService";
import Schema from "@/views/Company/CurrencyManagement/CurrencyManagementSchema";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
import { ShowAlert } from "../../../assets/common/vue-common";
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            recordId:0,
            showCurrencyManagement:false,
            noRecord:false,
            isLoading: false,
            CompanyId: null,
            CurrencyManagementData: [],
            CurrencyList: [],
            selectedCurrency: "Select",
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            topSearchFields: Schema.topsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            searchcondition: '',
            MODULENAME: null,
            SUBMODULECODE: null,
            privilegeParams: null,
            sharetype: '',
            Headers: [],
            filteredHeaders: [],
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
        }
    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=CurrencyManagement&action=`;
        await ths.FetchData();
        Schema.CurrencybindCommondropdown();
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
        ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
            const filteredItem = { ...item };

            // Check if USER_EMP_ID is null or empty, then replace with '--'
            if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                filteredItem.USER_EMP_ID = '--';
            }

            return filteredItem;
            });
        },
        
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            console.log(item);
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        // AddRow(e) {
        //     var vm = this;
        //     console.log(vm.CurrencyManagementData);
        //     vm.CurrencyManagementData.push({
        //         currency_log_id: '',
        //         source_currency_id: '',
        //         destination_currency_id: '',
        //         currency_rate: '',
        //     })
        // },
        AddRow: function (e) {
            var vm = this;
            console.log(vm.CurrencyManagementData);
            vm.CurrencyManagementData.push({
                "source_currency_id": "{\"Name\":null,\"value\":\"1\"}", "destination_currency_id": "{\"Name\":null,\"value\":\"\"}", "currency_rate": "0.00",
            });
            console.log(vm.CurrencyManagementData);
        },
        DeleteRow (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                vm.CurrencyManagementData.splice(id, 1);
            });
        },
        ChangeCurrencyUpdate(id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmManageCurrency"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                ShowAlert
            });
        },
        renderActions: function (row) {
            console.log(row);            
        },
        
        
        FetchData: async function () {
            var vueObj = this;
            this.isLoading = true;
            vueObj.CurrencyList = [];
            vueObj.CurrencyList = Schema.topsearchSchema[0].listOptions;
            console.log(vueObj.CurrencyList);
            var url=`CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}`;
            await CurrencyManagementDataService.CurrencyManagementListing(url)
                .then(function (response) {
                    const filteredColumnNames = ["source_currency_id", "destination_currency_id", "currency_rate", "Automatic"];
                    // Filter the original list based on the desired COLUMN_NAME values
                    vueObj.filteredHeaders =  response.data.FieldSchema.filter(obj => filteredColumnNames.includes(obj.COLUMN_NAME));
                    vueObj.Headers = vueObj.filteredHeaders.map((item) => item.DISPLAY_NAME);
                    vueObj.filteredHeaders.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "title") {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter,
                            };
                        }                        
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    });
                    // &searchCondition=${vueObj.searchCondition}&isPaged=${vueObj.isPaged}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}
                    if (response.data.Data != "" && response.data.Data != null) {
                        if (response.data.Data.length > 0) {
                            vueObj.CurrencyManagementData = response.data.Data;
                            vueObj.CurrencyManagementData.forEach(item => {
                                item.created_by = JSON.parse(item.created_by);
                                item.modified_by = JSON.parse(item.modified_by);
                                item.status_id = JSON.parse(item.status_id);
                                
                            });
                           
                            vueObj.TotalRecords = vueObj.CurrencyManagementData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.ReportData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }
                    }                    
                    else {
                        vueObj.noRecord = true;
                        vueObj.ReportData = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
            });
        },
        // closeModalSharePopup: function () {
        //     $("#modalshareview").modal('hide');
        //     this.showsharereportview = false;
        // },
        // getStatuslistForListing: function (StatusType, lang) {
        //     var vm = this;
        //     var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
        //         CurrencyManagementDataService.statusdropdown(url).then(function (response) {
        //         if (response.data.status == "Success") {
        //             var json = response.data.result;
        //             vm.StatusListForListing = json.data;
        //         }
        //     });
        // }
    }
}
</script>